body {
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.container {
  width: 310px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (width >= 640px) {
  .container {
    width: 640px;
  }
}

@media screen and (width >= 1246px) {
  .container {
    width: 1246px;
  }
}

.stars {
  z-index: -1;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.star {
  background-color: #fffc;
  border-radius: 50%;
  width: 2px;
  height: 2px;
  animation: 5s ease-in-out infinite twinkle;
  position: absolute;
}

@keyframes twinkle {
  0%, 100% {
    opacity: .8;
    transform: translate(0)scale(1);
  }

  50% {
    opacity: .3;
    transform: translate(10px, 10px)scale(1.5);
  }
}

@media screen and (width >= 640px) {
  body {
    background-image: url("bg-tablet.200fd420.png"), url("noise.2b9b397b.png");
    background-repeat: no-repeat, repeat-y;
    background-size: cover, contain;
  }
}

@media screen and (width >= 1246px) {
  body {
    background-image: url("bg-desktop.547dc6a3.png"), url("noise.2b9b397b.png");
  }
}

.header {
  background-image: url("pic-desktop@1x.58af34b5.png"), url("pic-desktop@1x.943d3e83.webp");
  background-repeat: no-repeat, no-repeat;
  background-size: cover, contain;
}

.header__sign {
  justify-content: end;
  gap: 15px;
  padding-top: 15px;
  display: flex;
}

.header__logo {
  position: absolute;
}

.header__central {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__title {
  text-align: center;
  width: 588px;
  font-family: "Montserrat" sans-serif;
  color: #dc56c5;
  margin-top: 40px;
  margin-bottom: 52px;
  font-size: 50px;
  font-weight: 900;
}

.header__sign-in, .header__sign-up {
  cursor: pointer;
  text-align: center;
  color: #fff;
  background-color: #0000;
  border: #0000;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
}

.header__inputs {
  flex-direction: row;
  gap: 30px;
  display: flex;
}

.header__search, .header__country {
  text-align: center;
  color: #939393;
  backdrop-filter: blur(20px);
  background: #fff3;
  border: 1px solid #939393;
  border-radius: 25px;
  width: 270px;
  height: 40px;
  font-size: 16px;
  font-weight: 400;
}

.header__btn {
  cursor: pointer;
  text-align: center;
  font-family: "Montserrat" sans-serif;
  text-transform: uppercase;
  color: #fff;
  background: #0006;
  border: 2px solid #dc56c5;
  border-radius: 25px;
  align-items: center;
  margin-bottom: 89px;
  padding: 4px 4px 4px 10px;
  font-size: 13px;
  font-weight: 500;
  transition: background-color .3s, color .3s, border-color .3s, padding .3s;
  display: flex;
}

.header__btn-text {
  margin: 0;
  font-size: 9px;
  transition: color .3s;
}

.header__circle {
  background-color: #fff;
  border-radius: 50px;
  width: 16px;
  height: 16px;
  transition: background-color .3s, margin .5s;
}

.header__icon {
  margin: 0 0 0 10px;
}

.header .header__btn-dark .header__circle {
  background-color: #fff;
  margin-left: 6px;
  margin-right: 0;
}

.header .header__btn-light {
  order: 1;
  padding-left: 4px;
  padding-right: 10px;
}

.header .header__btn-light .header__circle {
  background-color: #fff3;
  order: -1;
  margin-left: 0;
  margin-right: 6px;
}

@media screen and (width <= 779px) {
  .header__logo {
    position: absolute;
  }

  .header__sign {
    display: none;
  }

  .header__central {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    display: flex;
  }

  .header_title {
    text-align: center;
    width: 285px;
    font-family: "Montserrat" sans-serif;
    margin-top: 40px;
    font-size: 18px;
