  }

  .header__sign-in, .header_sign-up {
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    text-decoration: none;
  }

  .header__inputs {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 133px;
    display: flex;
  }

  .header__search, .header__country {
    text-align: center;
    color: #939393;
    border: 1px solid #939393;
    border-radius: 25px;
    min-width: 270px;
    height: 40px;
    font-size: 16px;
    font-weight: 400;
  }

  .header__btn {
    cursor: pointer;
    text-align: center;
    width: 134px;
    height: 24px;
    font-family: "Montserrat" sans-serif;
    text-transform: uppercase;
    color: #fff;
    background: #fff3;
    border: 2px solid #dc56c5;
    border-radius: 25px;
    margin-bottom: 59px;
    font-size: 13px;
    font-weight: 500;
  }
