  color: #fff;
}

.back-to-top {
  position: fixed;
  bottom: 10px;
  right: 26px;
  width: 70px;
  height: 70px;
  border: 3px solid #a259ff;
  background-color: transparent;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: border-color 0.3s ease, transform 0.3s ease;
  z-index: 999;
  opacity: 0;
  visibility: hidden;

  &.visible {
    opacity: 1;
    visibility: visible;
  }

  &:hover {
    border-color: #fff;
    transform: translateY(-4px);
  }

  svg {
    width: 40px;
    height: 40px;
    stroke: #a259ff;
  }
}
